.formulario {
  flex: 1;
  width: 95%;
  display: flex;
  margin: auto;
}

.formulario__text {
  width: 55%;
}

.formulario__formBox {
  width: 45%;
  border-radius: 1.6rem;
  background: linear-gradient(0deg, rgba(255, 162, 9, 1) 0%, rgba(255, 195, 115, 1) 100%);
  padding: 7rem;
}

.formulario__form {
  display: flex;
  flex-direction: column;
}

.formulario__label {
  display: none;
  font-size: 2.8rem;
}

.formulario__entrada {
  border: none;
  outline: none;
  border-bottom: 1px solid rgba(255, 255, 255, .5);
  border-radius: .8rem;
  background: rgba(255, 255, 255, .3);
  backdrop-filter: blur(5px);
  padding: 1rem;
  margin-bottom: 2rem;
}

.formulario__boton {
  border: none;
  border-radius: 1.4rem;
  background-color: #9af476;
  padding: 1.5rem 0;
  margin-top: 1rem;
}

/* Estilos para pantallas pequeñas */
@media (max-width: 450px) {
  .formulario {
    flex-direction: column;
    align-items: center;
  }
  
  .formulario__text, .formulario__formBox {
    width: 95%;
  }

  .formulario__formBox {
    padding: 7rem 3rem;
  }
}

@media (min-width: 451px) and (max-width: 600px) {
  .formulario {
    flex-direction: column;
    align-items: center;
  }
  
  .formulario__text, .formulario__formBox {
    width: 95%;
  }

  .formulario__formBox {
    padding: 7rem 4rem;
  }
}

/* Estilos para pantallas medianas */
@media (min-width: 601px) and (max-width: 1024px) {
  .formulario {
    flex-direction: column;
    align-items: center;
  }

  .formulario__text, .formulario__formBox {
    width: 95%;
  }
}

/* Estilos para pantallas grandes */
@media (min-width: 1025px) and (max-width: 1164px) {
  .formulario {
    flex-direction: column;
    align-items: center;
  }

  .formulario__text, .formulario__formBox {
    width: 95%;
  }
}

@media (min-width: 1165px) and (max-width: 1245px) {
  .formulario {
    flex-direction: column;
    align-items: center;
  }
}

